.App {
  font-family: sans-serif;
  text-align: center;
}
.embed-container {
  position: relative;
  padding-bottom: 80%;
  height: 0;
  max-width: 100%;
}
.embed-container iframe,
.embed-container object,
.embed-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  border: 5px double black;
  width: 100%;
  height: 100%;
}
small {
  position: absolute;
  z-index: 40;
  bottom: 0;
  margin-bottom: -15px;
}
.dashboard {
  padding: 15px;
  position: relative;
}

.graph-container {
  float: left;
  width: 65%;
}

.image-link-macro,
.image-link-macro img {
  display: block;
  width: 100%;
}
.image-link-macro {
  position: auto;
  text-decoration: none;
  height: 100%;
}
img {
  width: relative;
  height: auto;
  vertical-align: middle;
  border: 0;
}

.package {
  position: relative;
  padding: var(--padding, 15px);
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  align-items: var(--pa, flex-start);
  color: var(--tc);
}
.package > :first-child {
  margin-top: 0;
}

.package > * {
  margin: var(--ps, 10px) 0 0;
}

.card {
  display: flex;
  flex-direction: column;
}
.card,
.paper {
  position: relative;
  background-color: var(--paper-color, #fff);
  box-shadow: var(--paper-shadow);
}
/* *,
:after,
:before {
  box-sizing: inherit;
} */

.byline,
.kicker {
  display: inline-block;
  text-transform: uppercase;
}
.kicker {
  padding: 5px 8px;
  font-weight: 400;
  color: var(--tc, #222);
  border: 0.5px solid var(--tc, #222);
  border-radius: 2px;
  --lhc: var(--tc, #222);
  --ld: none;
  --lhd: none;
}
.h6,
h6 {
  font-size: 0.75rem;
}
.h5,
.h6,
h5,
h6 {
  --hf: var(--sans);
}

